body {
  font-family: 'Roboto', sans-serif;
  background: #fff;
  font-size: 16px;
  padding: 30px;
}

img,
video {
  padding: 10px;
}

button {
  display: block;
  /* margin-left: auto;
  margin-right: auto; */
}

button[data-action='load-more'].is-hidden {
  display: none;
}

button[data-action='load-more'] > .spinner-grow.is-hidden {
  display: none;
}

.error {
  color: red;
}
